import React, {Component} from 'react'
import Link, { withPrefix } from 'gatsby-link'
import Helmet from 'react-helmet'
import AppIcons from '../partials/AppIcons'
import SocialLinks from '../partials/SocialLinks'
import ContactLinks from '../partials/ContactLinks'

const displayBlock = {
  display: 'block'
}

const displayNone = {
  display: 'none'
}

class WorkplacePage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      modalToggle: false
    }

    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    if(e.target.className === 'workplace-modalbg' || e.target.className === 'modal-close') {
      this.setState({
        modalToggle: false
      });
    } else {
      this.setState({
        modalToggle: true
      });
    }
  }

  render() {
    return (
      <div>
        <Helmet
          title="Fitato for Workplace"
          meta={[
            { name: 'description', content: 'The Fitato for Workplace program is a corporate health and wellness program designed specially for the modern Indian workplace. ' },
          ]}
        />
        <div className="workplace-page">
          
          <div className="workplace-section-intro" style={{backgroundImage: 'url(' + withPrefix('/static/workplace/yoga-1.jpeg') + ')'}}>
            <div className="container">
              <div className="workplace-section-intro__content">
                <h1>Activate<br />a Healthy Workplace.</h1>
                <p>Get the Fitato advantage for your employees and see the happiness, health and productivity soar!</p>
                <span className="button" style={{marginLeft: '0'}} onClick={this.handleClick}>Learn More</span>
              </div>
            </div>
          </div>
    
          <div className="workplace-section-onepass" style={{backgroundImage: 'url(' + withPrefix('/static/gradient-bg.png') + ')'}}>
            <div className="container">
              <h2>One Pass | Limitless Fitness Options</h2>
            </div>
          </div>
          
          <div className="workplace-section-about">
            <div className="container">
              <h2>What is Fitato for Workplace?</h2>
              <p>Fitato is the city’s largest Fitness network. A single membership access to gyms and fitness studios across the city - so your employees can access whichever gym they prefer, whenever they prefer.</p>
              <p>What’s more, our customised corporate plans bring you a variety of on-demand fitness session right at your workplace.</p>
              <p>This is the corporate fitness program for the new age offices!</p>
              <span className="button" onClick={this.handleClick}>Get in touch</span>
            </div>
          </div>
    
          <div className="workplace-section-employees">
            <div className="container">
              <h2 className="align-center">The Fitato Advantage</h2>
              <div className="workplace-section-employees__row">
                <div>
                  <h3>For Employees</h3>
                  <p>Provide your team members with a flexible fitness plan they’ll actually love. Customised fitness options - right from Yoga to MMA, Crossfit to Swimming, there’s something for everyone!</p>
                  <p>Fitato is the fitness plan created for for the modern employee.</p>
                </div>
                <div className="workplace-section-employees__row--img">
                  <img src={withPrefix('/static/workplace/002-employee.png')} />
                </div>
              </div>
              <div className="workplace-section-employees__row">
                <div className="workplace-section-employees__row--img workplace-section-employees__row--img2">
                  <img src={withPrefix('/static/workplace/001-flats.png')} />
                </div>
                <div class="workplace-section-employees__row--c2">
                  <h3>For Employers</h3>
                  <p>It’s very difficult to make everyone happy. And run it well! Fitato For Workplaces is built for the new age workplace. All the stats you need, on a single dashboard. Plus a range of in-office activities you can choose from.</p>
                  <p>Healthy was never this easy!</p>
                </div>
              </div>
            </div>
          </div>
    
          <div className="workplace-section-why">
            <div className="container">
              <h2>The Corporate Wellness Program<br />for the Indian Workplace</h2>
              <div className="workplace-section-why__items">
                <div className="workplace-section-why__item">
                  <div className="workplace-section-why__item--left">
                    <img src={withPrefix('/static/icons/card.svg')} />
                  </div>
                  <div className="workplace-section-why__item--right">
                    <p>Cost-effecient plans, perfect for organizations.</p>
                  </div>
                </div>
                <div className="workplace-section-why__item">
                  <div className="workplace-section-why__item--left">
                    <img src={withPrefix('/static/icons/barbell.svg')} />
                  </div>
                  <div className="workplace-section-why__item--right">
                    <p>On-demand fitness for everyone in your organisation.</p>
                  </div>
                </div>
                <div className="workplace-section-why__item">
                  <div className="workplace-section-why__item--left">
                    <img src={withPrefix('/static/icons/face.svg')} />
                  </div>
                  <div className="workplace-section-why__item--right">
                    <p>Variety of activities makes fitness fun.</p>
                  </div>
                </div>
                <div className="workplace-section-why__item">
                  <div className="workplace-section-why__item--left">
                    <img src={withPrefix('/static/icons/clock.svg')} />
                  </div>
                  <div className="workplace-section-why__item--right">
                    <p>Offers flexibility of time and place.</p>
                  </div>
                </div>
              </div>
              <span className="button align-center" onClick={this.handleClick}>Arrange a Call Back</span>
            </div>
          </div>
    
          <div className="workplace-section-classes">
            <div className="container">
              <h2>Variety of On-Demand Classes</h2>
              <div className="workplace-section-classes__items">
                <div className="workplace-section-classes__item">
                  <p>Yoga</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Self-Defence</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>MMA</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Zumba</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Meditation</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Aqua Zumba Event</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Capoeira</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Drumcircles</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Kho Kho</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>HIIT</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Functional Training</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Bokwa</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Piloxing</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Biet Workshop</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Dance</p>
                </div>
                <div className="workplace-section-classes__item">
                  <p>Aerobics</p>
                </div>
              </div>
            </div>
          </div>

          <div className="workplace-section-clients">
            <div className="container">
              <h2>Our Clients</h2>
              <div className="workplace-section-clients__items">
                <div className="workplace-section-clients__item">
                  <img src={withPrefix('/static/workplace/ola-cabs.svg')} alt="ola"/>
                </div>
                <div className="workplace-section-clients__item">
                  <img src={withPrefix('/static/workplace/2000px-Infosys_logo.svg.png')} alt="infosys"/>
                </div>
                <div className="workplace-section-clients__item">
                  <img src={withPrefix('/static/workplace/Logotype_digital_black_large.png')} alt="uber"/>
                </div>
                <div className="workplace-section-clients__item">
                  <img src={withPrefix('/static/workplace/tieto_logo_blue_3.png')} alt="tieto"/>
                </div>
                <div className="workplace-section-clients__item">
                  <img src={withPrefix('/static/workplace/zocdoc-logo-large.png')} alt="zocdoc"/>
                </div>
                <div className="workplace-section-clients__item">
                  <img src={withPrefix('/static/workplace/bajaj-finserv_416x416.png')} alt="bajaj finserv"/>
                </div>
              </div>
            </div>
          </div>
    
          <div className="workplace-section-happy">
            <div className="container">
              <h2>Testimonials</h2>
              <p><em>"Our team has loved such an innovative and personalized approach to wellness. It gives us all the opportunity to go to new styles of fitness we've been wanting to try for a while. Best of all, many of our employees use Fitato to stay active together."</em></p>
            </div>
          </div>
    
          <div className="workplace-section-plans">
            <div className="container">
              <h2>Our Membership Plans</h2>
              <div className="workplace-section-plans__items">
                <div className="workplace-section-plans__item">
                  <div className="workplace-section-plans__item--title">
                    <h3>Individual Employee Plan</h3>
                  </div>
                  <div className="workplace-section-plans__item--content">
                    <p>Offer for individual employees</p>
                    <p>Complete access to Fitato brank network</p>
                    <p>Membership purchased by individual</p>
                    <p><strong>Access 250+ Gyms &amp; Fitness Studios</strong></p>
                    <span className="button button-fluid" onClick={this.handleClick}>Get Started</span>
                  </div>
                </div>
                <div className="workplace-section-plans__item">
                  <div className="workplace-section-plans__item--title">
                    <h3>Corporate All Access Pass</h3>
                  </div>
                  <div className="workplace-section-plans__item--content">
                    <p>Package to be purchased by the company</p>
                    <p>Premium Fitato access and a complete wellness plan</p>
                    <p>Monthly fitness/wellness sessions in office campus</p>
                    <p><strong>Additional Diet &amp; Wellness Sessions</strong></p>
                    <span className="button button-fluid" onClick={this.handleClick}>Get Started</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
    
          <div className="workplace-section-app">
            <div className="container">
              <div className="workplace-section-app__item">
                <h2>Health and Fitness, was never this easy.</h2>
                <p>Become a Fitato member, choose your next workout from many categories and reserve the activity — all through the Fitato app.</p>
                <AppIcons />
              </div>
              <div className="workplace-section-app__item align-center">
                <img src={withPrefix('/static/index/section-four-2.png')} alt="fitato smartphone app" />
              </div>
            </div>
          </div>
    
          <div className="workplace-section-cta">
            <div className="container">
              <h2>The Corporate Wellness Program<br />You Need At Your Workplace</h2>
              <span className="button" onClick={this.handleClick}>Let us help</span>
            </div>
          </div>
    
        </div>

        <div className="workplace-modalbg" 
          style={(this.state.modalToggle) ? displayBlock : displayNone} onClick={this.handleClick}>
          <div className="workplace-modal">
            <div className="workplace-modal__title">
              <h3>Learn More About the Fitato Corporate Program</h3>
            </div>
            <span className="modal-close" onClick={this.handleClick}>X</span>
            <div className="workplace-modal__form">
              <small>Please fill out this form, and we&apos;ll get in touch shortly.</small>
              <form action="https://formspree.io/pooja.ahuja@fitato.fit" method="POST" className="contact-form">
                <div><input type="text" name="name" placeholder="Your Name" required /></div>
                <div><input type="text" name="cname" placeholder="Company Name" required /></div>
                <div><input type="tel" name="phone" placeholder="Phone Number" required /></div>
                <div><input type="email" name="_replyto" placeholder="Business Email" required /></div>
                <div><input type="number" name="team-size" placeholder="Team Size" required /></div>
                <input type="hidden" name="_subject" value="Workplace form submission" />
                <input type="hidden" name="_cc" value="murada@fitato.fit" />
                <input type="submit" value="Submit" />
              </form>
            </div>
          </div>
        </div>

        <div className="footer-info">
          <div className="container">
            <div className="footer-info__col1">
              <img src={withPrefix('/static/fitato-logo-dark.png')} alt="fitato" style={{width: '120px'}}/>
              <p>The Fitato for Workplace program is a corporate health and wellness program designed specially for the modern Indian workplace. Diet plans, workout plans, team building exercises - it's the only wellness membership your office will ever need!</p>
            </div>
            <div className="footer-info__col2">
              <h4>Learn More</h4>
              <ul className="footer-ul">
                <li><a href="http://blog.fitato.fit">Blog</a></li>
                <li><a href="https://www.fitato.fit/faq">FAQ</a></li>
                <li><a href="https://www.fitato.fit/reviews">Reviews</a></li>
                <li><a href="https://www.fitato.fit/privacy-policy">Privacy Policy</a></li>
              </ul>
            </div>
            <div className="footer-info__col3">
              <h4>Contact</h4>
              <ContactLinks cname="footer-ul" />
              <SocialLinks cname="footer-social" />
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default WorkplacePage



